<template>
  <div>
    <div id="exactly" style="height:300px;">{{this.exacttly}}</div>
  </div>
</template>

<script>
import emitter from "@/bus.ts";
export default {
  data() {
    return {
      exacttly: "中国，以华夏文明为源泉、中华文化为基础，是世界上历史最悠久的国家之一。中国各族人民共同创造了光辉灿烂的文化，具有光荣的革命传统。中国是以汉族为主体民族的多民族国家，通用汉语、汉字，汉族与少数民族统称为“中华民族”，又自称“炎黄子孙”、“龙的传人”，同时中国也是世界四大文明古国之一。"
    };
  },
  mounted() {
    this.check();
  },
  methods: {
    check() {
      emitter.on("exacttly", s => {
        this.exacttly = s;
      });
    }
  }
};
</script>

