<template>
  <div class="boxnav" id="info11" style="height:300px;"></div>
</template>

<script>
import * as echarts from "echarts";
import emitter from "@/bus.ts";
export default {
  data() {
    return {
      square: 960
    };
  },
  mounted() {
    this.info();
    this.check();
  },
  methods: {
    check() {
      emitter.on("square", s => {
        if (isNaN(s) == false) {
          this.square = s;
          this.info();
        }
      });
    },
    info() {
      var chartDom = document.getElementById("info11");
      var myChart = echarts.init(chartDom);
      var option;
      option = {
        tooltip: {
          formatter: "{a} <br/>{b} : {c}万平方公里"
        },
        series: [
          {
            name: "国土面积",
            type: "gauge",
            min: 0,
            max: 2000,
            radius: "100%",
            title: {
              textStyle: {
                fontWeight: "bolder",
                fontSize: 22
              }
            },
            progress: {
              show: true
            },
            detail: {
              valueAnimation: true,
              formatter: "{value}"
            },
            data: [
              {
                value: this.square,
                name: "国土面积",
                itemStyle:{color:'#AC453D'}
              }
            ]
          }
        ]
      };
      option && myChart.setOption(option);
    }
  }
};
</script>