<template>
  <div class="boxnav" id="heliu" style="height:300px;"></div>
</template>

<script>
import * as echarts from "echarts";
import def from "@/assets/世界综合国力/default";
import 夏 from "@/assets/世界综合国力/夏";
import 商 from "@/assets/世界综合国力/商";
import 周 from "@/assets/世界综合国力/周";
import 西晋 from "@/assets/世界综合国力/西晋";
import 北宋 from "@/assets/世界综合国力/北宋";
import 明朝 from "@/assets/世界综合国力/明朝";
import 东晋 from "@/assets/世界综合国力/东晋";
import 南北朝 from "@/assets/世界综合国力/南北朝";
import 南朝宋 from "@/assets/世界综合国力/南朝宋";
import 南宋 from "@/assets/世界综合国力/南宋";
import 清朝 from "@/assets/世界综合国力/清朝";
import 三国 from "@/assets/世界综合国力/三国";
import 隋朝 from "@/assets/世界综合国力/隋朝";
import 唐朝 from "@/assets/世界综合国力/唐朝";
import 五代十国 from "@/assets/世界综合国力/五代十国";
import 西楚 from "@/assets/世界综合国力/西楚";
import 西汉 from "@/assets/世界综合国力/西汉";
import 新朝 from "@/assets/世界综合国力/新朝";
import 元朝 from "@/assets/世界综合国力/元朝";
import 东汉 from "@/assets/世界综合国力/东汉";
import 秦 from "@/assets/世界综合国力/秦";
import 战国 from "@/assets/世界综合国力/战国";
import 春秋 from "@/assets/世界综合国力/春秋";
import emitter from "@/bus.ts";
export default {
  data() {
    return {
      msg: ""
    };
  },
  mounted() {
    this.heliu();
    this.check();
  },
  methods: {
    check() {
      emitter.on("moneyEvent", data => {
        if (data != "") {
          this.msg = data;
          this.heliu();
        }
      });
    },
    heliu() {
      var chartDom = document.getElementById("heliu");
      var myChart = echarts.init(chartDom);
      const option0 = def;
      const option1 = 夏;
      const option2 = 商;
      const option3 = 周;
      const option4 = 春秋;
      const option5 = 战国;
      const option6 = 秦;
      const option7 = 西楚;
      const option8 = 西汉;
      const option9 = 新朝
      const option10 = 东汉
      const option11 = 三国
      const option12 = 西晋
      const option13 = 东晋
      const option14 = 南朝宋
      const option15 = 南北朝
      const option16 = 隋朝
      const option17 = 唐朝
      const option18 = 五代十国
      const option19 = 北宋
      const option20 = 南宋
      const option21 = 元朝
      const option22 = 明朝
      const option23 = 清朝
      if(this.msg == "") myChart.setOption(option0);
      if (this.msg == "夏" || this.msg == "二头文化诞生" || this.msg == "鸣条之战" || this.msg == "鸣条之战追击") myChart.setOption(option1);
      if (this.msg == "殷商" || this.msg == "武丁即位" || this.msg == "牧野之战") myChart.setOption(option2);
      if (this.msg == "西周" || this.msg == "昭穆时代" || this.msg == "国人暴动" || this.msg == "周幽王之死" || this.msg == "平王东迁") myChart.setOption(option3);
      if (this.msg == "春秋" || this.msg == "召陵之盟" || this.msg == "践土之盟" || this.msg == "秦霸西戎" || this.msg == "楚王问鼎"|| this.msg == "三家分晋") myChart.setOption(option4);
      if (this.msg == "战国" || this.msg == "秦灭六国") myChart.setOption(option5);
      if (this.msg == "秦" || this.msg == "北击匈奴" || this.msg == "筑长城" || this.msg == "秦灭百越" || this.msg == "迁军岭南"|| this.msg == "陈胜吴广起义"|| this.msg == "秦投降刘邦") myChart.setOption(option6);
      if (this.msg == "西楚" || this.msg == "楚汉之争") myChart.setOption(option7);
      if (this.msg == "西汉" || this.msg == "刘邦称帝" || this.msg == "汉武盛世" || this.msg == "孝宣中兴" ) myChart.setOption(option8);
      if (this.msg == "新朝" || this.msg == "东汉开国") myChart.setOption(option9);
      if (this.msg == "东汉" || this.msg == "东汉统一战争" || this.msg == "黄巾起义" || this.msg == "董卓挟献帝"  || this.msg == "曹丕篡汉" ) myChart.setOption(option10);
      if (this.msg == "三国" || this.msg == "官渡之战" || this.msg == "赤壁之战" || this.msg == "夷陵之战"  || this.msg == "蜀吴结盟"|| this.msg == "蜀汉灭亡" || this.msg == "三国时代结束" || this.msg == "晋灭东吴") myChart.setOption(option11);
      if (this.msg == "西晋" || this.msg == "太康之治" || this.msg == "八王之乱" || this.msg == "五胡乱华"  || this.msg == "西晋灭亡" ) myChart.setOption(option12);
      if (this.msg == "东晋" || this.msg == "淝水之战" || this.msg == "东晋灭亡") myChart.setOption(option13);
      if (this.msg == "南朝宋" || this.msg == "北魏一统华北") myChart.setOption(option14);
      if (this.msg == "南北朝" || this.msg == "北魏分裂" || this.msg == "东魏灭亡，北齐建立" || this.msg == "西魏灭亡，北周建立"  || this.msg == "北齐灭亡" || this.msg == "大隋开国" ) myChart.setOption(option15);
      if (this.msg == "隋朝" || this.msg == "隋灭陈" || this.msg == "开皇之治" || this.msg == "隋末农民起义"  || this.msg == "江都之变" || this.msg == "唐建立" ) myChart.setOption(option16);
      if (this.msg == "唐朝" || this.msg == "贞观之治" || this.msg == "唐高宗四面扩张" || this.msg == "武周代唐"  || this.msg == "开元盛世" || this.msg == "安史之乱"|| this.msg == "唐末农民起义"|| this.msg == "唐朝灭亡" ) myChart.setOption(option17);
      if (this.msg == "五代十国" || this.msg == "陈桥兵变" ) myChart.setOption(option18);
      if (this.msg == "北宋" || this.msg == "北宋统一全国" || this.msg == "澶州之盟" || this.msg == "庆历和议"  || this.msg == "方腊起义" || this.msg == "靖康之变" || this.msg == "南宋建立" ) myChart.setOption(option19);
      if (this.msg == "南宋" || this.msg == "临安沦陷"|| this.msg == "南宋、元朝"|| this.msg == "建立大元"|| this.msg == "崖山之战") myChart.setOption(option20);
      if (this.msg == "元朝" || this.msg == "元统一"|| this.msg == "元成宗治国"|| this.msg == "窝阔台汗国灭亡"|| this.msg == "南坡之变"|| this.msg == "明朝开国"|| this.msg == "元朝、明朝") myChart.setOption(option21);
      if (this.msg == "明朝" || this.msg == "元灭亡，明建立"|| this.msg == "明征安南战争"|| this.msg == "明东南沿海抗倭之战"|| this.msg == "明缅战争"|| this.msg == "万历朝鲜战争"|| this.msg == "萨尔浒之战"|| this.msg == "清建立"|| this.msg == "明朝，清朝"|| this.msg == "明亡，清顺治元年") myChart.setOption(option22);
      if (this.msg == "清朝" || this.msg == "郑成功收复台湾"|| this.msg == "台湾正式回归"|| this.msg == "雅克萨之战"|| this.msg == "廓尔喀之役"|| this.msg == "第一次鸦片战争"|| this.msg == "第二次鸦片战争"|| this.msg == "清军收复新疆之战"|| this.msg == "中法战争"|| this.msg == "中日甲午战争"|| this.msg == "八国联军侵华战争") myChart.setOption(option23);
    }
  }
};
</script>

<style scoped>
div {
  position: absolute;
	left: 50%;
	top: 70%;
  width: 100%;
	transform: translate(-50%, -50%);
}
</style>