<template>
  <div class="boxnav" id="control11" style="height:550px;"></div>
</template>

<script>
import * as echarts from "echarts";
import emitter from "@/bus.ts";
import defined from "@/defined.js";
import result from "@/assets/result";
export default {
  data() {
    return {
      childNum: 0
    };
  },
  mounted() {
    this.control();
  },
  methods: {
    control() {
      var chartDom = document.getElementById("control11");
      var myChart = echarts.init(chartDom);
      var option;
      var data = result;
      option = {
        series: {
          type: "sunburst",
          data: data,
          radius: [0, "180%"],
          sort: undefined,
          emphasis: {
            focus: "ancestor"
          },
          levels: [
            {},
            {
              r0: "0%",
              r: "60%",
              itemStyle: {
                borderWidth: 2
              },
              label: {
                align: "right"
              }
            },
            {
              r0: "60%",
              r: "65%",
              itemStyle: {
                borderWidth: 2
              },
              label: {
                position: "outside"
              }
            }
          ]
        }
      };
      myChart.on("click", params => {
        defined.setWar(params.name);
        defined.setSquare(params.data.square);
        defined.setexacttly(params.data.exacttly);
        emitter.emit("moneyEvent", defined.war);
        emitter.emit("square", defined.square);
        emitter.emit("exacttly", defined.exacttly);
      });
      option && myChart.setOption(option);
    }
  }
};
</script>
